const Issue = props => {
  const { user, title, created_at, updated_at } = props;
  // avatar from user obj
  // lastUpdated in text form 2 hours ago or 3 days ago
  return (
    <div>
      <img src={user.avatar_url} height="40" />
      <h2>{title}</h2>
      <span>{created_at}</span>
      <span>{updated_at}</span>
    </div>
  );
};

export default Issue;
